<template>
  <div class="bg">
    <div class="title">
      <Breadcrumb>
        <BreadcrumbItem to="/user/project-index">我的项目</BreadcrumbItem>
        <BreadcrumbItem>{{projectData.name}} 合作协议</BreadcrumbItem>
      </Breadcrumb>
    </div>
    <div class="contetn">
      <div class="seach">
        <i-Select
          v-model="seachConfig.status"
          style="width:150px;margin-right: 10px;float:left;"
          placeholder="所有项目类型"
        >
          <i-Option value="-1">所有状态</i-Option>
          <i-Option value="0">待确认</i-Option>
          <i-Option value="1">已确定</i-Option>
          <i-Option value="2">被拒绝</i-Option>
        </i-Select>
        <Input
          style="width:614px;float:left;margin-top: -1px;"
          search
          enter-button
          placeholder="资金方名字（为空搜索所有项目合同）"
          v-model="seachConfig.name"
          @on-search="getData"
        />
      </div>
      <div class="cl">
        <br />
        <Button icon="md-add" type="primary" @click="modelConfig.show = true">发起合作协议</Button>
      </div>
      <div class="list">
        <ul>
          <li
            v-for="(item, index) in data"
            :key="index"
            @click="treatyData = item ; modelConfig = {show:true,nav:2}"
          >
            <p class="img">
              <a
                :style="item.status == 0 ? 'color:#f39800;' : item.status == 1 ? 'color:#8fc31f;' : item.status == 2 ? 'color:#e60012;' : ''"
              >{{item.status == 0 ? '待确认' : item.status == 1 ? '已确认' : item.status == 2 ? '被拒绝' : '无效状态' }}</a>
              <img src="../../../assets/img/file_img.png" alt />
            </p>
            <p class="title">{{item.mmName}}</p>
          </li>
        </ul>
        <div class="cl"></div>
        <div class="page">
          <Page :total="seachConfig.pageTotal" @on-change="pageChange" show-elevator />
        </div>
      </div>
    </div>
    <!-- 发起投资协议 -->
    <Modal
      width="760"
      :styles="{top: '150px'}"
      v-model="modelConfig.show"
      :mask-closable="false"
      :title="modelConfig.nav == 1 ? '添加协议' : modelConfig.nav == 2 ? '协议详情' : '错误'"
    >
      <div class="form" v-if="modelConfig.nav == 1">
        <i-form ref="formValidate" :model="formValidate" :rules="ruleValidate" :label-width="180">
          <div>
            <form-item label="项目名称" @submit.native.prevent>
              <Input disabled :placeholder="projectData.name" style="width:450px" />
            </form-item>
            <form-item label="接受资金方" prop="mmCode" @submit.native.prevent>
              <i-Select
                v-model="formValidate.mmCode"
                placeholder="输入资金名字进行搜索"
                filterable
                remote
                :remote-method="remoteMethod1"
                style="width:450px"
              >
                <i-Option
                  v-for="(option, index) in options1"
                  :value="option.code"
                  :key="index"
                >{{option.institutions}}</i-Option>
              </i-Select>
            </form-item>
            <form-item label="投资金额" prop="money" @submit.native.prevent>
              <Input v-model="formValidate.money" placeholder="投资金额" style="width:150px">
                <span slot="append">万元</span>
              </Input>
            </form-item>
            <form-item label="投资占比" prop="proportion" @submit.native.prevent>
              <Input v-model="formValidate.proportion" placeholder="百分比" style="width:100px">
                <span slot="append">%</span>
              </Input>
            </form-item>
            <form-item label="流程证明 ( 格式: 照片 )" @submit.native.prevent prop="transactionCertificate">
              <label>
                <Upload
                  accept="image/*"
                  :action="fileconfig.fileUrl"
                  :headers="fileconfig.fileHead"
                  name="file"
                  ref="transactionCertificate"
                  :on-success="transactionCertificate"
                  :show-upload-list="false"
                >
                  <Button icon="ios-cloud-upload-outline">流程证明</Button>
                  <div v-if="formValidate.transactionCertificate != ''">
                    <p>{{formValidate.transactionCertificate}}</p>
                    <p style="color: #2868d1;">*已添加,想更换直接上传新流程证明即可</p>
                  </div>
                </Upload>
              </label>
            </form-item>
            <form-item label="合同文件 ( 格式: 文件 )" @submit.native.prevent prop="contractFile">
              <label>
                <Upload
                  :action="fileconfig.fileUrl"
                  :headers="fileconfig.fileHead"
                  name="file"
                  :on-success="contractFile"
                  ref="contractFile"
                  :show-upload-list="false"
                >
                  <Button icon="ios-cloud-upload-outline">合同文件</Button>
                  <div v-if="formValidate.contractFile != ''">
                    <p>{{formValidate.contractFile}}</p>
                    <p style="color: #2868d1;">*已添加,想更换直接上传新合同文件即可</p>
                  </div>
                </Upload>
              </label>
            </form-item>
          </div>
        </i-form>
      </div>
      <div class="detail" v-else>
        <ul>
          <li>
            <span>合同状态:</span>
            <Tag
              checkable
              :color="treatyData.status == 0 ? 'warning' : treatyData.status == 1 ? 'success' : treatyData.status == 2 ? 'error' : ''"
            >{{treatyData.status == 0 ? '待确认' : treatyData.status == 1 ? '已确认' : treatyData.status == 2 ? '已拒绝' : '无效状态'}}</Tag>
          </li>
          <li>
            <span>项目名称:</span>
            {{projectData.name}}
          </li>
          <li>
            <span>接受资金方名称:</span>
            {{treatyData.mmName}}
          </li>
          <li>
            <span>投资金额:</span>
            {{treatyData.money}}万元
          </li>
          <li>
            <span>投占比列:</span>
            {{treatyData.proportion}}%
          </li>
          <li>
            <span>交易流水证明:</span>
            <Button type="info" @click="clickOpen(treatyData.transactionCertificate)">查看交易流水照片</Button>
          </li>
          <li>
            <span>合同文件:</span>
            <Button type="info" @click="clickOpen(treatyData.contractFile)">查看合同文件</Button>
          </li>
          <li>
            <span>发送时间:</span>
            {{treatyData.createTime}}
          </li>
        </ul>
      </div>
      <div class="cl"></div>
      <div slot="footer">
        <div v-if="modelConfig.nav == 1">
          <Button @click="modelConfig.show = false">取消</Button>
          <Button type="primary" @click="handleSubmit('formValidate')">确认发起提交</Button>
        </div>
        <div v-else>
          <Button @click="modelConfig.show = false">关闭</Button>
        </div>
      </div>
    </Modal>
  </div>
</template>
<script>
import {
  Icon,
  Button,
  Page,
  Input,
  Modal,
  Select,
  Tag,
  Option,
  Form,
  Upload,
  FormItem,
  Breadcrumb,
  BreadcrumbItem
} from "iview";
import {
  getRate,
  getProjectUser,
  projectAddTermSheet
} from "../../../api/project";
import { searchName } from "../../../api/capital";
import { fileUploadUrl, messages, imgUrl, validateNub } from "../../../utils";
export default {
  components: {
    [Icon.name]: Icon,
    [Page.name]: Page,
    [Input.name]: Input,
    [Tag.name]: Tag,
    [Modal.name]: Modal,
    [Select.name]: Select,
    [Option.name]: Option,
    [Form.name]: Form,
    [Upload.name]: Upload,
    [FormItem.name]: FormItem,
    [Breadcrumb.name]: Breadcrumb,
    [BreadcrumbItem.name]: BreadcrumbItem,
    [Button.name]: Button
  },
  data() {
    return {
      imgUrl: imgUrl,
      // 资金方数组
      options1: [],
      // 文件上传配置
      fileconfig: {
        fileUrl: fileUploadUrl,
        fileHead: {
          token: document.cookie.match(
            new RegExp("(^| )" + "token" + "=([^;]*)(;|$)")
          )[2]
        }
      },
      //弹出框配置
      modelConfig: {
        show: false,
        nav: 1 //1显示添加协议 2显示协议详情
      },
      //   获取到的合同数据
      data: [],
      //搜索配置
      seachConfig: {
        code: "",
        name: "",
        status: "-1",
        pageNo: 1, //当前页
        pageTotal: 1 //总条数
      },
      //项目基础信息
      projectData: {},
      //单个协议信息
      treatyData: {},
      //表单提交信息
      formValidate: {
        transactionCertificate: "",
        contractFile: "",
        pmCode: "",
        mmCode: "",
        money: "",
        proportion: ""
      },
      //表单验证
      ruleValidate: {
        transactionCertificate: [
          { required: true, message: "流水证明,不能为空", trigger: "blur" }
        ],
        contractFile: [
          { required: true, message: "投资合同,不能为空", trigger: "blur" }
        ],
        mmCode: [
          {
            required: true,
            message: "投递资金方,不能为空",
            trigger: "change"
          }
        ],
        money: [
          {
            required: true,
            message: "投资金额必填,必须为数字,最多保留两位小数",
            trigger: "blur",
            validator: validateNub
          }
        ],
        proportion: [
          {
            required: true,
            message: "投资占比必填,必须为数字,最多保留两位小数",
            trigger: "blur",
            validator: validateNub
          }
        ]
      }
    };
  },
  methods: {
    clickOpen(url) {
      window.open(imgUrl + url);
    },
    //获取项目数据
    getData() {
      getRate(this.seachConfig).then(res => {
        this.data = res.data.data.rows;
        this.seachConfig.pageTotal = res.data.data.total;
      });
    },
    //分页点击
    pageChange(page) {
      this.seachConfig.pageNo = page;
      this.getData();
    },
    //流水证明上传
    transactionCertificate(res) {
      if (res.status == "SUCCESS") {
        this.formValidate.transactionCertificate = res.data.url;
      }
    },
    //合同证明上传
    contractFile(res) {
      if (res.status == "SUCCESS") {
        this.formValidate.contractFile = res.data.url;
      }
    },
    //搜索资金方
    remoteMethod1(query) {
      if (query !== "") {
        searchName({ name: query }).then(res => {
          this.options1 = res.data.data;
        });
      }
    },
    //协议提交
    handleSubmit(name) {
      this.$refs[name].validate(valid => {
        if (valid) {
          projectAddTermSheet(this.formValidate).then(res => {
            if (res.data.status == "SUCCESS") {
              //成功将创建数组还原
              this.formValidate = {
                transactionCertificate: "",
                contractFile: "",
                pmCode: this.projectData.code,
                mmCode: "",
                money: "",
                proportion: ""
              };
              this.$refs.transactionCertificate.clearFiles();
              this.$refs.contractFile.clearFiles();
              messages.success(`投资协议发送成功`);
              this.getData();
              this.modelConfig.show = false;
            }
          });
        }
      });
    }
  },
  created() {
    //   获取项目基础信息
    getProjectUser({ id: this.$route.params.id }).then(res => {
      this.projectData = res.data.data;
      this.seachConfig.code = this.projectData.code;
      this.formValidate.pmCode = this.projectData.code;
      this.getData();
    });
  }
};
</script>
<style lang="less" scoped>
.bg {
  min-height: 600px;
  background: #ffffff;
  padding: 20px;
  .title {
    border-left: 5px solid #575757;
    padding-left: 10px;
    margin-bottom: 20px;
    color: #2868d1;
    font-size: 16px;
  }
  .list {
    margin-top: 40px;
    ul {
      li {
        width: 16.66%;
        float: left;
        text-align: center;
        padding-top: 20px;
        p {
          display: block;
          width: 100%;
        }
        .img {
          position: relative;
          a {
            position: absolute;
            bottom: 13px;
            right: 34px;
          }
        }
        .title {
          padding: 20px 10px 0px 10px;
          border: none;
          -webkit-word-break: break-all;
          -moz-word-break: break-all;
          -ms-word-break: break-all;
          -o-word-break: break-all;
          word-break: break-all;
          font-size: 12px;
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 2;
          -webkit-box-orient: vertical;
          color: #575757;
        }
      }
      li:hover {
        background: #eaf3fd;
        cursor: pointer;
      }
    }
  }
  .page {
    margin-top: 30px;
    padding-bottom: 60px;
    width: 100%;
    text-align: center;
  }
}
.detail {
  ul {
    li {
      float: left;
      width: 100%;
      padding: 10px 10%;
      font-size: 14px;
      img {
        width: 300px;
      }
      span {
        width: 150px;
        display: block;
        float: left;
      }
    }
  }
}
</style>